.Grid {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    background: #0D1F2C;
    background-size: cover;
}

.Images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(3, 1fr);
    height: 90vh;
    width: 90%;
}

.Images > div:first-child {
    grid-row: 1 / 3;
    grid-column: 1 / 3;
}

.Images > div:nth-child(5) {
    grid-column: 5 / 7;
}

.Images > div:nth-child(4) {
    grid-column: 3 / 5;
    grid-row: 2 / 4;
}

.Images > div:nth-child(10) {
    grid-column: 5 / 7;
}

@media screen and (max-width: 1023px) {
    .Grid {
        height: auto;
    }
    .Images {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: auto;
        width: 100%;
    }

    .Images > div:last-child,
    .Images > div:nth-last-child(2) {
        display: none;
    }
}
