.Grid-item {
    background-color: #ECECEC;
    background-size: cover;
    background-position: center;
    filter: blur(2px) sepia(35%);
    transition: filter, 500ms, ease-in;
    cursor: pointer;
}

.Grid-item:hover {
    filter: none;
}

@media screen and (max-width: 1023px) {
    .Grid-item {
        display: block;
        height: 90vw;
        width: 90vw;
    }
}
