.Parallax {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100vh;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: var(--yellow);
}

.Parallax__title {
    font-size: 92px;
}


@media screen and (max-width: 1023px) {
    .Parallax__title {
        font-size: 50px;
    }
}
