.Title {
    background: linear-gradient(rgba(236, 236, 236, 0.9), rgba(236, 236, 236, 0.9)), url("../../assets/bg.jpg") no-repeat center;
    background-size: cover;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    text-align: center;
}

.City-pictures {
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.City-pic {
    width: 400px;
    max-width: 100%;
    height: 174px;
    background-size: contain;
    background-position: bottom;
    background-repeat: no-repeat;
    opacity: 0.3;
}

.City-pic.london {
    background-image: url("./london.svg");
}

.City-pic.barcelona {
    background-image: url("./barcelona.svg");
}

.Hashtag {
    margin: 0;
    line-height: 1.1;
    text-transform: uppercase;
    color: var(--blue);
}

.yellow {
    color: var(--yellow);
}


@media screen and (max-width: 1023px) {
    .Title {
        padding: 15px;
    }

    .City-pictures {
        max-width: 100%;
    }

    .Hashtag {
        font-size: 40px;
    }
}
