.Details {
    background: linear-gradient(rgba(236, 236, 236, 0.9), rgba(236, 236, 236, 0.9)), url("../../assets/bg.jpg") no-repeat center;
    background-size: cover;
    height: 100vh;
    padding: 30px;
    text-align: center;
}

.Details__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 20px 50px;
}

.Details__title {
    color: var(--blue);
    margin-top: 0;
}


@media screen and (max-width: 1023px) {
    .Details {
        padding: 15px;
        height: auto;
    }

    .Details__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
