:root {
  --yellow: #E8CA82;
  --blue: #2F576E;
  --black: #0D1F2C;
  --orange: #D2963B;
  --grey: #ECECEC;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: Neucha, sans-serif;
  font-size: 24px;
  color: #0D1F2C;
  color: var(--black);
  line-height: 1.4;
  /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",*/
    /*"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",*/
    /*sans-serif;*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-size: 92px;
}

h2 {
  font-size: 40px;
}

.App {
  text-align: center;
}

.Main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Title {
    background: -webkit-linear-gradient(rgba(236, 236, 236, 0.9), rgba(236, 236, 236, 0.9)), url(/sannikov-wedding/static/media/bg.ebec6839.jpg) no-repeat center;
    background: linear-gradient(rgba(236, 236, 236, 0.9), rgba(236, 236, 236, 0.9)), url(/sannikov-wedding/static/media/bg.ebec6839.jpg) no-repeat center;
    background-size: cover;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;
    text-align: center;
}

.City-pictures {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}

.City-pic {
    width: 400px;
    max-width: 100%;
    height: 174px;
    background-size: contain;
    background-position: bottom;
    background-repeat: no-repeat;
    opacity: 0.3;
}

.City-pic.london {
    background-image: url(/sannikov-wedding/static/media/london.c7cdc8c2.svg);
}

.City-pic.barcelona {
    background-image: url(/sannikov-wedding/static/media/barcelona.0823af74.svg);
}

.Hashtag {
    margin: 0;
    line-height: 1.1;
    text-transform: uppercase;
    color: var(--blue);
}

.yellow {
    color: var(--yellow);
}


@media screen and (max-width: 1023px) {
    .Title {
        padding: 15px;
    }

    .City-pictures {
        max-width: 100%;
    }

    .Hashtag {
        font-size: 40px;
    }
}

.Grid {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;
    background: #0D1F2C;
    background-size: cover;
}

.Images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(3, 1fr);
    height: 90vh;
    width: 90%;
}

.Images > div:first-child {
    grid-row: 1 / 3;
    grid-column: 1 / 3;
}

.Images > div:nth-child(5) {
    grid-column: 5 / 7;
}

.Images > div:nth-child(4) {
    grid-column: 3 / 5;
    grid-row: 2 / 4;
}

.Images > div:nth-child(10) {
    grid-column: 5 / 7;
}

@media screen and (max-width: 1023px) {
    .Grid {
        height: auto;
    }
    .Images {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
        height: auto;
        width: 100%;
    }

    .Images > div:last-child,
    .Images > div:nth-last-child(2) {
        display: none;
    }
}

.Grid-item {
    background-color: #ECECEC;
    background-size: cover;
    background-position: center;
    -webkit-filter: blur(2px) sepia(35%);
            filter: blur(2px) sepia(35%);
    -webkit-transition: 500ms, ease-in, -webkit-filter;
    transition: 500ms, ease-in, -webkit-filter;
    transition: filter, 500ms, ease-in;
    transition: filter, 500ms, ease-in, -webkit-filter;
    cursor: pointer;
}

.Grid-item:hover {
    -webkit-filter: none;
            filter: none;
}

@media screen and (max-width: 1023px) {
    .Grid-item {
        display: block;
        height: 90vw;
        width: 90vw;
    }
}

.Details {
    background: -webkit-linear-gradient(rgba(236, 236, 236, 0.9), rgba(236, 236, 236, 0.9)), url(/sannikov-wedding/static/media/bg.ebec6839.jpg) no-repeat center;
    background: linear-gradient(rgba(236, 236, 236, 0.9), rgba(236, 236, 236, 0.9)), url(/sannikov-wedding/static/media/bg.ebec6839.jpg) no-repeat center;
    background-size: cover;
    height: 100vh;
    padding: 30px;
    text-align: center;
}

.Details__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 20px 50px;
}

.Details__title {
    color: var(--blue);
    margin-top: 0;
}


@media screen and (max-width: 1023px) {
    .Details {
        padding: 15px;
        height: auto;
    }

    .Details__grid {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
    }
}

.Parallax {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    height: 100vh;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: var(--yellow);
}

.Parallax__title {
    font-size: 92px;
}


@media screen and (max-width: 1023px) {
    .Parallax__title {
        font-size: 50px;
    }
}

.Invitation {
    background: -webkit-linear-gradient(rgba(236, 236, 236, 0.9), rgba(236, 236, 236, 0.9)), url(/sannikov-wedding/static/media/bg.ebec6839.jpg) no-repeat center;
    background: linear-gradient(rgba(236, 236, 236, 0.9), rgba(236, 236, 236, 0.9)), url(/sannikov-wedding/static/media/bg.ebec6839.jpg) no-repeat center;
    background-size: cover;
    height: 100vh;
    text-align: center;
    padding: 100px 30%;
}

@media screen and (max-width: 1023px) {
    .Invitation {
        padding: 15px;
    }
}

.History {
    background: -webkit-linear-gradient(rgba(236, 236, 236, 0.9), rgba(236, 236, 236, 0.9)), url(/sannikov-wedding/static/media/bg.ebec6839.jpg) no-repeat center;
    background: linear-gradient(rgba(236, 236, 236, 0.9), rgba(236, 236, 236, 0.9)), url(/sannikov-wedding/static/media/bg.ebec6839.jpg) no-repeat center;
    background-size: cover;
    height: 100vh;
    padding: 30px;
    text-align: center;
}

